import React, { useCallback, useReducer } from "react";

import deleteIcon from "images/delete.svg";
import swapIcon from "images/swap.svg";
import { createReducer, useDefaults, useElementHandlers } from "./hooks";
import "./styles.scss";
import {
  T_FavoriteItemWithValue,
  T_UnitConverterProps,
  T_UnitConverterState
} from "./types";
import * as CONST_UNIT_CONVERTER from "constants/tools/unitConverter";

export default function UnitConverter(props: T_UnitConverterProps) {
  const defaults = useDefaults(props.defaults);
  const memoizedReducer = useCallback(createReducer(props), [
    props.conversionFormulas,
    props.conversionRates
  ]);
  const [state, dispatch] = useReducer(memoizedReducer, {
    inputCode: defaults.inputCode,
    targetCode: defaults.targetCode,
    inputValue: "",
    targetValue: "",
    favorites: defaults.favorites
  });
  const {
    handleInputValueChange,
    handleTargetValueChange,
    handleListItemAction,
    handleInputClearClick,
    handleTargetClearClick,
    handleInputTargetCodeSwapClick,
    inputValueEl,
    targetValueEl
  } = useElementHandlers(dispatch, defaults.pageCache);

  return (
    <div className="MoneyEx">
      <div className="gridContainer">
        <div className="inputContainer">
          {[
            /**
             * Input unit
             */
            {
              code: state.inputCode,
              valueEl: inputValueEl,
              value: state.inputValue ?? (state.targetValue === 0 ? 0 : ""),
              placeholder: "input...",
              onClearClick: handleInputClearClick,
              onValueChange: handleInputValueChange
            },
            /**
             * Target unit
             */
            {
              code: state.targetCode,
              valueEl: targetValueEl,
              value: state.targetValue ?? (state.inputValue === 0 ? 0 : ""),
              placeholder: "target...",
              onClearClick: handleTargetClearClick,
              onValueChange: handleTargetValueChange
            }
          ].map((inputField, index) => {
            const unitMapInfo = props.allUnitsMap[inputField.code];
            const hasIcon = props.UnitIconComponent || unitMapInfo.icon;

            return (
              <div key={inputField.code} className="inputItem">
                {props.UnitIconComponent && unitMapInfo.altCode ? (
                  <props.UnitIconComponent
                    code={unitMapInfo.altCode}
                    wrapperClassName="inputUnitIconComponent"
                  />
                ) : (
                  unitMapInfo.icon
                )}
                <span className="inputItemCodeSymbol">
                  {hasIcon ? `${inputField.code} ` : ""}
                  {unitMapInfo.symbol}
                </span>
                <input
                  autoFocus={index === 0}
                  ref={inputField.valueEl}
                  maxLength={12}
                  inputMode="numeric"
                  placeholder={inputField.placeholder}
                  className="inputField"
                  type="text"
                  onChange={inputField.onValueChange}
                  value={inputField.value}
                />
                <img
                  src={deleteIcon}
                  onClick={inputField.onClearClick}
                  className={`clearInputIcon ${
                    inputField.value !== "" ? "visible" : "invisible"
                  }`}
                  alt="close"
                />
              </div>
            );
          })}
          <span className="inputItemsSeparator" />
          <img
            src={swapIcon}
            className="swapIcon"
            alt="Swap Icon"
            onClick={() =>
              handleInputTargetCodeSwapClick(state.inputCode, state.targetCode)
            }
          />
          {/* <button className="swapIcon inputContainerItemsOrdering">SWAP</button> */}
        </div>
        <div className="favList">
          {getUnitListMarkup({
            favorites: state.favorites,
            allUnitsMap: props.allUnitsMap,
            handleListItemAction,
            currentInputCode: state.inputCode,
            currentTargetCode: state.targetCode,
            UnitIconComponent: props.UnitIconComponent
          })}
        </div>
      </div>
    </div>
  );
}

function getUnitListMarkup({
  handleListItemAction,
  allUnitsMap,
  favorites,
  currentInputCode,
  currentTargetCode,
  UnitIconComponent
}: {
  handleListItemAction: (
    event: React.MouseEvent<HTMLButtonElement, MouseEvent>
  ) => void;
  allUnitsMap: T_UnitConverterProps["allUnitsMap"];
  favorites?: T_UnitConverterState["favorites"];
  currentInputCode?: T_UnitConverterState["inputCode"];
  currentTargetCode?: T_UnitConverterState["targetCode"];
  UnitIconComponent?: T_UnitConverterProps["UnitIconComponent"];
}) {
  const arrSource = favorites || Object.keys(allUnitsMap);

  return (
    <>
      {arrSource.map((unit) => {
        const code = favorites
          ? (unit as T_FavoriteItemWithValue).code
          : (unit as string);

        // Do not render the currency item that is already the input/target
        if (code === currentInputCode || code === currentTargetCode) {
          return null;
        }

        const unitMapInfo = allUnitsMap[code];
        const hasIcon = UnitIconComponent || unitMapInfo.icon;

        return (
          <div className="itemContainer" key={code}>
            <div className="itemInfoContainer">
              <div className="codeFlagGroup">
                {hasIcon ? (
                  <>
                    {UnitIconComponent && unitMapInfo.altCode ? (
                      <UnitIconComponent
                        code={unitMapInfo.altCode}
                        wrapperClassName="-mt-1 pr-2"
                      />
                    ) : (
                      unitMapInfo.icon
                    )}
                    {code}
                  </>
                ) : (
                  <span className="text-xl">{unitMapInfo.symbol}</span>
                )}
              </div>
              <div className="favItemRightGroup">
                {favorites && (
                  <div className="favItemSymbolValueGroup">
                    {hasIcon && (
                      <span className="favItemSymbol">
                        {unitMapInfo.symbol || ""}
                      </span>
                    )}
                    <span className="exRate">
                      &nbsp;
                      {(unit as T_FavoriteItemWithValue).value ?? 0}
                    </span>
                  </div>
                )}
                <div className="name">
                  {(code && unitMapInfo?.name) || "..."}
                </div>
              </div>
            </div>
            <div className="favItemActionBtnGroup">
              {[
                CONST_UNIT_CONVERTER.E_UnitConverterListItemActions.input,
                CONST_UNIT_CONVERTER.E_UnitConverterListItemActions.target
              ].map((btnName) => (
                <button
                  key={btnName}
                  className="favItemActionBtn"
                  data-code={code}
                  data-action-name={btnName}
                  onClick={handleListItemAction}
                >
                  {btnName}
                </button>
              ))}
            </div>
          </div>
        );
      })}
    </>
  );
}
